<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Edit Recurring Detail') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="heading-border mb-2">
                            <h5 class="h5"><span>Recurring</span></h5>
                        </div>
                        <dynamic-form lang="en" :schema="FormSchema"></dynamic-form>

                        <div class="heading-border mb-2">
                            <h5 class="h5"><span>Scheduled Appointment</span></h5>
                        </div>
                        <dynamic-form lang="en" v-on:OnSubmit="SubmitRecurringDetail" :buttons="buttons"
                            :schema="ScheduledFormSchema">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        TicketId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            ticketId: 0,
            isWeekely: 0,
            isBiWeekely:0,
            dateField: 0,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Frequency'),
                            name: "Frequency",
                            value: "",
                            config: {
                                options: [
                                    { name: "Daily", value: "Daily" }, { name: "Weekly", value: "Weekly" },
                                    { name: "BiWeekly", value: "BiWeekly" }, { name: "Monthly", value: "Monthly" },
                                    { name: "Quarterly", value: "Quarterly" }, { name: "halfYearly", value: "halfYearly" },
                                    { name: "Annually", value: "Annually" }],
                                onChange: this.ShowWeeklyDateData
                            },
                            placeholder: this.$t('Select Frequency'),
                            validationRules: { "required": true, max: 100 }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Weekely'),
                            name: "Weekely",
                            value: "",
                            config: {
                                options: [
                                    { name: "Monday", value: 1 }, { name: "Tuesday", value: 2 },
                                    { name: "Wednesday", value: 3 }, { name: "Thursday", value: 4 },
                                    { name: "Friday", value: 5 }, { name: "Saturday", value: 6 },
                                    { name: "Sunday", value: 7 }]
                            },
                            placeholder: this.$t('Select option'),                                                    
                            visibility: false,
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Bi-Weekely'),
                            name: "Weekely",
                            value: "",
                            config: {
                                options: [
                                    { name: "Monday", value: 1 }, { name: "Tuesday", value: 2 },
                                    { name: "Wednesday", value: 3 }, { name: "Thursday", value: 4 },
                                    { name: "Friday", value: 5 }, { name: "Saturday", value: 6 },
                                    { name: "Sunday", value: 7 }]
                            },
                            placeholder: this.$t('Select Day'),                            
                            visibility:this.isBiWeekely == 1 ? true : false,
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Date'),
                            name: "Monthly",
                            value: "",
                            config: { format: "M/D/YYYY" },
                            placeholder: this.$t(''),                           
                            visibility:this.dateField > 0 ? true : false,
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Number of Recurrences'),
                            name: "NumberofRecurrences",
                            value: "",
                            placeholder: this.$t(''),
                            validationRules: { "required": false, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ name: "Active", value: 1001 }, { name: "Stopped", value: 1003 }]
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": false, max: 100 },
                        }
                    ]
                }
            ],

            ScheduledFormSchema: [
                {
                    layoutType: "triple",
                    Data: [

                        {
                            astype: "DateField",
                            label: this.$t('Scheduled Start Time'),
                            name: "ScheduledStartTime",
                            value: '',
                            placeholder: this.$t('StartDateTime'),
                            config: { format: "M/D/YYYY" }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Scheduled End Time'),
                            name: "ScheduledEndTime",
                            value: '',
                            placeholder: this.$t('StartDateTime'),
                            config: { format: "M/D/YYYY" }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Service Resource:'),
                            name: "ServiceResource",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": false, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Service Crew'),
                            name: "ServiceCrew",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": false, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
      //  debugger;
        var vm = this;
        vm.ticketId = vm.TicketId;
        vm.getResourceList();
        vm.getcrewList();
        setTimeout(function () {
            vm.RecurrTicketDetail();
        }, 500);
    },
    methods: {
        ShowWeeklyDateData: function(e,field)
        {
            debugger;
            var vm = this;
            if(field.value != null && field.value != '')
            {
                if(field.value == 'Weekly')
                {                    
                    vm.FormSchema[0].Data[1].visibility=true;
                    vm.FormSchema[0].Data[2].visibility=false;
                    vm.FormSchema[0].Data[3].visibility=false;
                } 
                else if(field.value == 'BiWeekly')
                {
                    vm.FormSchema[0].Data[1].visibility=false;
                    vm.FormSchema[0].Data[2].visibility=true;
                    vm.FormSchema[0].Data[3].visibility=false;
                }
                else if(field.value == "Monthly" || field.value == "Quarterly" || field.value == "halfYearly" || field.value == "Annually")
                {
                    vm.FormSchema[0].Data[1].visibility=false;
                    vm.FormSchema[0].Data[2].visibility=false;
                    vm.FormSchema[0].Data[3].visibility=true;
                }
                else{
                    vm.FormSchema[0].Data[1].visibility=false;
                    vm.FormSchema[0].Data[2].visibility=false;
                    vm.FormSchema[0].Data[3].visibility=false;
                }
            }           
        },
        getResourceList: async function () {
           // debugger;
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_SA_RESOURCENAME'}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.ScheduledFormSchema[0].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        RecurrTicketDetail: function () {
            debugger;
            var vm = this;
            vm.recurrTicketView = this.$parent.recurringTicketViewDetail[0];
            vm.FormSchema[0].Data[0].value = this.$parent.recurringTicketViewDetail[0].FREQUENCY;
            vm.FormSchema[0].Data[1].value = this.$parent.recurringTicketViewDetail[0].NO_OF_RECURRANCE;
            vm.FormSchema[1].Data[0].value = this.$parent.recurringTicketViewDetail[0].REC_STATUS_ID;
            if ( vm.FormSchema[0].Data[0].value == "Weekly") {
                var weekDaysId = this.$parent.recurringTicketViewDetail[0].WEEK_DAYS;   
                vm.FormSchema[0].Data[1].value = weekDaysId;
                vm.FormSchema[0].Data[1].visibility=true;
                vm.FormSchema[0].Data[2].visibility=false;
                vm.FormSchema[0].Data[3].visibility=false;
            }
            else if ( vm.FormSchema[0].Data[0].value == "BiWeekly") {
                vm.FormSchema[0].Data[2].value = this.$parent.recurringTicketViewDetail[0].WEEK_DAYS;
                vm.FormSchema[0].Data[1].visibility=false;
                vm.FormSchema[0].Data[2].visibility=true;
                vm.FormSchema[0].Data[3].visibility=false;

            }
            else if (vm.FormSchema[0].Data[0].value == "Monthly" ||  vm.FormSchema[0].Data[0].value == "Quarterly" ||  vm.FormSchema[0].Data[0].value == "halfYearly" ||  vm.FormSchema[0].Data[0].value == "Annually") {
                vm.FormSchema[0].Data[3].value = this.$parent.recurringTicketViewDetail[0].DATE;
                vm.FormSchema[0].Data[1].visibility=false;
                vm.FormSchema[0].Data[2].visibility=false;
                vm.FormSchema[0].Data[3].visibility=true;
            }

            vm.ScheduledFormSchema[0].Data[0].value = this.$parent.recurringTicketViewDetail[0].SCHEDULE_START_DATETIME;
            vm.ScheduledFormSchema[0].Data[1].value = this.$parent.recurringTicketViewDetail[0].SCHEDULE_END_DATETIME;

            if (this.$parent.recurringTicketViewDetail[0].CREW_ID != null) {              
                vm.ScheduledFormSchema[1].Data[0].value = this.$parent.recurringTicketViewDetail[0].CREW_ID;
            }


            if (this.$parent.recurringTicketViewDetail[0].RESOURCE_ID != null) {
                vm.ScheduledFormSchema[0].Data[2].value = this.$parent.recurringTicketViewDetail[0].RESOURCE_ID;
            }
            if (vm.scheduledStartDateTime != null && vm.scheduledEndDateTime != null) {
                vm.divScheduledTime = true;
            }

        },
        getcrewList: async function () {
           // debugger;
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_CREWNAME'}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item) => {
                            vm.ScheduledFormSchema[1].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },

        Close: function () {
            this.$parent.showRecurringTicketEditModal = false;
        },
        SubmitRecurringDetail: function () {
           // debugger;
            var vueObj = this;          
            var obj = {
                RefId: vueObj.ticketId,
                FrequencyId: vueObj.FormSchema[0].Data[0].value,                
                WeeklyId: vueObj.FormSchema[0].Data[1].value ,
                BiWeeklyId: vueObj.FormSchema[0].Data[2].value,
                MonthlyDate: vueObj.FormSchema[0].Data[3].valuee,
                NoOfRecurrence: vueObj.FormSchema[0].Data[4].value,
                ResourceId: vueObj.ScheduledFormSchema[0].Data[2].value > 0 ? vueObj.ScheduledFormSchema[0].Data[2].value  : 0,
                CrewId: vueObj.ScheduledFormSchema[1].Data[0].value > 0 ? vueObj.ScheduledFormSchema[1].Data[0].value  : 0,
                ScheduledStartDateTime: vueObj.ScheduledFormSchema[0].Data[0].value,
                ScheduledEndDateTime: vueObj.ScheduledFormSchema[0].Data[1].value,
                StatusId: vueObj.FormSchema[1].Data[0].value,
            }
            var objectPostString = JSON.stringify(obj);
            //vueObj.isSending = true;
            DataServices.UpdateRecurringDetail(objectPostString).then(function (response) {
                if (response.data != null && response.data != "") {
                    //debugger;
                    if (response.data.status == "Success") {
                        vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("RecurringDetail")]), 'success', true, vueObj.$t('Alert'));
                        vueObj.$parent.openRecurringTicketQuickView();
                        vueObj.$emit('close');                        

                    } else {
                        vueObj.ShowAlert(vueObj.$t("ErrorWhileSavingRecords"), 'failure', true, vueObj.$t('Alert'));
                    }
                } else {
                    vueObj.ShowAlert(vueObj.$t("ErrorWhileSavingRecords"), 'failure', true, vueObj.$t('Alert'));
                }
            });
        },

    },
}

</script>