    async function  bindleftCommonSearchdropdown(instance, moduleName, filtername,data,AllCount) {
        var vm = instance;
    var param = "moduleName='"+moduleName+"'&type="+filtername+""
    var param1 = "moduleName=Ticketing&type="+filtername+""
      if (filtername == 'Priority') {
        vm.leftSearchFields[0].listOptions.push({
            name: 'All('+AllCount+')',
            value:''
        });
        data.forEach(element => {
                let obj = {
                    name: element.mapping_name+'('+element.tab_count_data+')',
                    value: element.tab_mapping_id
                }
                vm.leftSearchFields[0].listOptions.push(obj);
            });
    }
    // else if(filtername=='FSM_TICKET_TYPE'){
    //     await DataServices.GetDDLData(param1).then((response) => {
    //         vm.leftSearchFields[4].listOptions=[];
    //         response.data.DATA.forEach(element => {
    //             let obj = {
    //                 name: element.name,
    //                 value: element.value
    //             }
    //             vm.leftSearchFields[4].listOptions.push(obj);
    //         });
    //     });
    // }
    else if(filtername=='TICKET_RESOLVER'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[5].listOptions=[];
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[5].listOptions.push(obj);
            });
        });
    }   
    else if(filtername=='Ticketing'){
        var stausParams="statusType=Ticketing"
        await DataServices.GetFsmStatusDDLData(stausParams).then((response) => {
            vm.leftSearchFields[6].listOptions=[];
            response.data.forEach(element => {
                let obj = {
                    name: element.STATUS,
                    value: element.STATUS
                }
                vm.leftSearchFields[6].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='TICKET_SUPERVISOR'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[7].listOptions=[];
            if(response.data && response.data.hasOwnProperty('DATA') ){
                vm.leftSearchFields[7].listOptions.push({
                    name:'All',value:'All'
                })
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[7].listOptions.push(obj);
            });
        }
        });
    }
    else if(filtername=='Critical Approval'){
        vm.leftSearchFields[9].listOptions=[];
        let obj = {
            name: 'ALL',
            value: 'ALL'
        }
        let obj2 = {
            name: 'Approval Pending',
            value: 'APPROVE_PENDING'
        }
        let obj3 = {
            name: 'Approved',
            value: 'APPROVED'
        }
        vm.leftSearchFields[9].listOptions.push(obj);
        vm.leftSearchFields[9].listOptions.push(obj2);
        vm.leftSearchFields[9].listOptions.push(obj3); 
    }
    else if(filtername=='Form Submit'){
        vm.leftSearchFields[8].listOptions=[];
        let obj = {
            name: 'Yes',
            value: 1
        }
        let obj2 = {
            name: 'No',
            value: 0
        }
        vm.leftSearchFields[8].listOptions.push(obj);
        vm.leftSearchFields[8].listOptions.push(obj2);
    }
    else if(filtername=='TAG_DATA'){
        await DataServices.GetDDLData(param1).then((response) => {  
            vm.leftSearchFields[9].listOptions=[];          
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[9].listOptions.push(obj);
            });
        });
    }
     else if(filtername=='Pending Acceptance'){
        vm.leftSearchFields[10].listOptions=[];
        let obj = {
            name: 'All',
            value: 2
        }
        let obj2 = {
            name: 'Yes',
            value: 1
        }  
        let obj3 = {
            name: 'No',
            value: 0
        }        
        vm.leftSearchFields[10].listOptions.push(obj);
        vm.leftSearchFields[10].listOptions.push(obj2);
        vm.leftSearchFields[10].listOptions.push(obj3);
    }

    else if(filtername=='Recurring Type'){
        vm.leftSearchFields[11].listOptions=[];
        let obj = {
            name: 'ParentRecurringTicket',
            value: 1
        }
        let obj2 = {
            name: 'RecurringTicket',
            value: 2
        }        
        vm.leftSearchFields[11].listOptions.push(obj);
        vm.leftSearchFields[11].listOptions.push(obj2);
        // vm.leftSearchFields[13].listOptions.push(obj3);
    }
    else if(filtername=='FSM_USERS'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[12].listOptions=[];
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                //vm.leftSearchFields[15].listOptions.push(obj);
                vm.leftSearchFields[12].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='DEPARTMENT'){
        await DataServices.GetDDLData(param1).then((response) => {    
            vm.leftSearchFields[14].listOptions=[];       
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[14].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='TICKET_CATEGORY'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[15].listOptions=[];
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[15].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='CLIENT'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[16].listOptions=[];
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[16].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='PRODUCT'){
        await DataServices.GetDDLData(param1).then((response) => {
            vm.leftSearchFields[17].listOptions=[];
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[17].listOptions.push(obj);
            });
        });
    }
        
    };
    
