<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Comment') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">                    
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchemaData" v-on:OnSubmit="SaveRejectInfo">

                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";
import Schema from "@/views/Ticketing/TicketingSchema";
export default {    
    props: {
        TicketId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,            
            ticketId: 0,
            listheaderbuttons: null,
            listActions: null,            
            SortBy: '',
            SortExp: '',
            Headers: [],
            FormSchemaData: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            mode: "Comment",
                            label: this.$t('Comment '),
                            name: "Comment",
                            value: "",
                            config: {
                                options: []
                            },
                            //placeholder: this.$t('Comment'),
                            validationRules: { "required": true },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",                   
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.ticketId = vm.TicketId;
    },
    methods: {
        Close: function () {
            this.$parent.showRejectTicketModal = false;
        },
        SaveRejectInfo: function () {
            debugger;
            var vm = this;
            var comment = vm.FormSchemaData[0].Data[0].value;            
                if (comment != null) {
                    //debugger;
                    vm.$parent.AcceptRejectAction(vm.$parent.SelectedItem, "reject", comment);
                }
                else {
                    vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                }            
        },
    }
}
</script>