    methods: {
        RemoveTag: function (event, item) {
            var vueObj = this;
            vueObj.confirmR(vueObj.$t("ConfirmTag"), true, 'Confirm', function () {
                var url = `TicketId=${item.ticket_id}&TagId=${item.tag_id}`;
                DataServices.RemoveTagData(url).then(response => {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.ShowAlert(vueObj.$t('RemovedSuccess'), vueObj.$t('Tag')), "success", true, vueObj.$t('Alert');
                            vueObj.fetchRecords();
                        }
                    }
                });
            });
        },
        SetStyle: function (item) {
            if (item.bg_color == null || item.bg_color == '') {
                return "'background':'#17a2b8','color':'#464646'"
            }
            else {
                return `'background': ${item.bg_color},'color':${tagItem.font_color}`;
            }
        },
        ShowDeleteButton: function (item) {
            //  debugger;
            var vm = this;
            if (this.UserType == 'CA' || this.UserType == 'SA' || this.UserType == 'PM' || this.UserType == "NU") {
                if (item.ticket_created_by != '@loggedUserInfo.ID' && item.status_code == 'TICKET_OPENED') {
                    return false;
                }
                if (item.totalcomments == 0 && item.is_field_service == false || (item.status_code == 'TICKET_DRAFT' && this.UserType != "NU")) {
                    return true;
                }
                if (item.ticket_created_by == this.userid && (this.UserType == "NU" && item.status_code == 'TICKET_DRAFT')) {
                    return true;
                }
            }
            else {
                return false;
            }
        },
        getAttachmentCount: function (data, type) {
            var rtnVal = 0
            var arr = JSON.parse(data);
            if (arr.length > 0) {
                arr.forEach(function (obj) {
                    if (obj.TYPE == "Image" && type == "Image") {
                        rtnVal += obj.COUNT;
                    } else if (obj.TYPE == "Video" && type == "Video") {
                        rtnVal += obj.COUNT;
                    } else if (obj.TYPE != "Image" && obj.TYPE != "Video" && type != "Image" && type != "Video") {
                        rtnVal += obj.COUNT;
                    }
                });
            }
            return rtnVal;
        },
        SetTicketColor: function (color) {
            if (color == '' || color == null || typeof (color) == 'undefined') {
                color = '#28a745';
            }
            return {
                border: '2px solid ' + color,
                borderTop: '5px solid ' + color
            };
        },
        ShowHideMap: function () {
            var icon = $('#icnToggleMap');
            if (this.isShowMap == false) {
                this.isShowMap = true;
                this.mapHeaderText = 'Collapse map';
                if (icon.hasClass('fa-angle-double-down')) {
                    icon.removeClass('fa-angle-double-down');
                    icon.addClass('fa-angle-double-up');
                }
            }
            else {
                this.isShowMap = false;
                this.mapHeaderText = 'Expand map';
                if (icon.hasClass('fa-angle-double-up')) {
                    icon.removeClass('fa-angle-double-up');
                    icon.addClass('fa-angle-double-down');
                }
            }
        },
        searchFunction: function (event) {
            var vueObj = this;
            vueObj.PageLevelSearchCondition = "";
            vueObj.IsSearchTrueOrFalse = true;
            vueObj.IsLocationSearch = false;
            vueObj.IsSubjectSearch = false;
            vueObj.IsAcceptedSearch = false;
            vueObj.IsTicketTextSearch = false;
            vueObj.IsTicketTypeSearch = false;
            vueObj.IsAssignedSearch = false;
            vueObj.IsStatusSearch = false;
            vueObj.IsSuperVisorSearch = false;
            vueObj.IsApprovalSearch = false;
            vueObj.IsFormSubmittedSearch = false;
            vueObj.IsTagSearch = false;
            vueObj.IsAnySearch = false;
            vueObj.IsRecurringSearch = false;
            vueObj.IsOpenByUserSearch = false;

            if ($.trim(vueObj.LocationValue) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsLocationSearch = true;
                vueObj.IsAnySearch = true;
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : ""; vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += "(ISNULL(TPIN.COUNTY,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.PLACE,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.ADDRNAME,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.ADDRFROM,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.DIGSTREET,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.STATE,'') LIKE '%25" + vueObj.LocationValue + "%25' ) ";
            }
            if ($.trim(vueObj.TicketType) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsTicketTypeSearch = true;
                vueObj.IsAnySearch = true;
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TPIN.TYPE_ID = " + vueObj.TicketType;
            }
            if ($.trim(vueObj.Subject) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsSubjectSearch = true;
                vueObj.IsAnySearch = true;
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TICK.TICKET_SUBJECT LIKE '%25" + vueObj.Subject + "%25'";
            }
            if ($.trim(vueObj.ticketNumber) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsTicketNumberSearch = true;
                vueObj.IsAnySearch = true;
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " (TICK.TICKET_NUMBER LIKE '%25" + vueObj.ticketNumber + "%25' OR ";
                vueObj.PageLevelSearchCondition += " TICK.EXTERNAL_TICKET_NUMBER LIKE '%25" + vueObj.ticketNumber + "%25')";
            }
            if ($.trim(vueObj.AssignedTo) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsAssignedSearch = true;
                vueObj.IsAnySearch = true;
                if (vueObj.AssignedTo == '0') {
                    vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                    vueObj.PageLevelSearchCondition += " isnull(TICK.Assigned_To,0)=0 ";
                }
                else if (vueObj.AssignedTo == 'ALL') {
                    vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                    vueObj.PageLevelSearchCondition += " ( isnull(TICK.Assigned_To,0) = 0 or TICK.Assigned_To>0) ";
                } else {
                    vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                    vueObj.PageLevelSearchCondition += " TICK.Assigned_To = " + vueObj.AssignedTo;
                }
            }
            if ($.trim(vueObj.Status) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsStatusSearch = true;
                vueObj.IsAnySearch = true;
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TICK.status_id in( " + vueObj.Status + " )";
            }
            if ($.trim(vueObj.TicketText) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsTicketTextSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.Supervisor) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsSuperVisorSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.OpenByUser) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsOpenByUserSearch = true;
                vueObj.IsAnySearch = true;
            } if ($.trim(vueObj.IsDepartment) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsDepartmentSearch = true;
                vueObj.IsAnySearch = true;
            } if ($.trim(vueObj.IsCategory) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsCategorySearch = true;
                vueObj.IsAnySearch = true;
            } if ($.trim(vueObj.IsClient) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsClientSearch = true;
                vueObj.IsAnySearch = true;
            } if ($.trim(vueObj.IsProduct) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsProductSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.OpenDate) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsOpenDateSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.WorkOrderApprovalPending) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsApprovalSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.IsFormSubmitted) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsFormSubmittedSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.IsFormSubmitted) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsFormSubmittedSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.Tags) != "") {
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.IsTagSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.IsAccepted) != "") {
                vueObj.IsAcceptedSearch = true;
                vueObj.IsAnySearch = true;
            }
            if ($.trim(vueObj.IsRecurring) != "") {
                vueObj.IsRecurringSearch = true;
                vueObj.IsAnySearch = true;
            }
            vueObj.CurrentPage = 1;
            //vueObj.PageSize = 10;
            vueObj.PageSize = this.PageSize;
            ////debugger;
            if (event) {
                vueObj.PageNumber = 1;
                vueObj.GroupStart = 1;
                vueObj.GroupEnd = 3;
            }
            else {
                vueObj.PageNumber = this.PageNumber;
            }
            //SetScrolbar('dark-thick', null, 'filterscroll');
            vueObj.fetchRecords();
            vueObj.SearchConditionByName = vueObj.SearchConditionByName;
            vueObj.ShowUserListData(false);
        },
        widgetClick: function (code) {
            var vueObj = this;
