        ManageLeftSearchFields() {
            if (this.CompanyType == 'normal') {
                var locationIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Location'));
                if (locationIndex > -1) {
                    this.leftSearchFields.splice(locationIndex, 1);
                }
                var formsubmitIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Form Submit'));
                if (formsubmitIndex > -1) {
                    this.leftSearchFields.splice(formsubmitIndex, 1);
                }
                var pendingIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Pending Acceptance'));
                if (pendingIndex > -1) {
                    this.leftSearchFields.splice(pendingIndex, 1);
                }
                var recurringIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Recurring Type'));
                if (recurringIndex > -1) {
                    this.leftSearchFields.splice(recurringIndex, 1);
                }
            }
            if (this.CompanyType != 'locating') {
                var ticketIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Ticket Text'));
                if (ticketIndex > -1) {
                    this.leftSearchFields.splice(ticketIndex, 1);
                }
                var ticketTypeIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Ticket Type'));
                if (ticketTypeIndex > -1) {
                    this.leftSearchFields.splice(ticketTypeIndex, 1);
                }
            }
            if (this.SearchStatusText != 'Closed and Locked' || this.SearchStatusText != 'Closed') {
                var closedateIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Close Date'));
                if (closedateIndex > -1) {
                    this.leftSearchFields.splice(closedateIndex, 1);
                }
            }
            if (this.usertype != 'CA') {
                var supervisorIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Supervisor'));
                if (supervisorIndex > -1) {
                    this.leftSearchFields.splice(supervisorIndex, 1);
                }
            }
            if (this.SeeAllPendingApproval != 'yes') {
                var criticalIndex = this.leftSearchFields.indexOf(this.leftSearchFields.find(ele => ele.fieldName == 'Critical Approval'));
                if (criticalIndex > -1) {
                    this.leftSearchFields.splice(criticalIndex, 1);
                }
            }
        },
