                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'On Hold':
                    returnClass = 'px-2 bg-warning text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Open':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Waiting':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'In progress':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Completed':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Cancelled':
