        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Accept':
                        action.isVisible = (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 1 && row.is_field_service == 'FSM' || row.is_field_service == true);
                        break;
                    case 'Reject':
                        action.isVisible = (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 1 && row.is_field_service == 'FSM' || row.is_field_service == false);
                        break;
                    case 'View on Map':
                        action.isVisible = row.is_field_service == 'FSM' ? true : false
                        break;
                    case 'Edit':
                        action.isVisible = row.status_code == 'TICKET_DRAFT';
                        break;
                    case 'Delete':
                        action.isVisible = self.usertype == 'CA' || self.usertype == 'SA' || self.usertype == 'PM'
                        break;
                    case 'Add a Follow-Up':
                        action.isVisible = row.status_code != 'TICKET_DRAFT'
                        break;
                    case 'View Recurring Detail':
                        action.isVisible = row.parent_recurring_ticket == 1;
                        break;
                    case 'Reopen':
                        action.isVisible = row.status_code === 'Ticket_completed' || row.status_code === 'TICKET_CLOSED';
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        AcceptRejectTicket: function (item, type) {
            var vm = this;
            var msg = "";
            var alertTitle = "";
            if (type == "Accept") {
                msg = vm.$t('Are you sure you want to Accept the Ticket?'), true, vm.$t('Alert');
                alertTitle = vm.$t('Accept');
            }
            else {
                msg = vm.$t('Are you sure you want to Reject the Ticket?'), true, vm.$t('Alert');
                alertTitle = vm.$t('Reject');
            }
            if (typeof (item) === "string") {
                item = JSON.parse(item);
            }
            vm.confirmR(msg, true, alertTitle, function () {
                if (type == "Reject") {
                    vm.SelectedItem = item;
                    vm.showRejectTicketModal = true;
                }
                else {
                    vm.AcceptRejectAction(item, type, "");
                }
            });
        },

        AcceptRejectAction: function (item, type, comment) {
            var vm = this;
            var postString = {
                TicketId: item.ticket_id,
                Type: type,
                Comment: comment
            };
            var objectPostString = JSON.stringify(postString);
            DataServices.AcceptRejectTicket(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1") {
                        vm.fetchRecords();
                        if (type == "reject") {
                            vm.ShowAlert(vm.$t('Ticket Rejected Successfully'), "success", true, vm.$t('Alert'));
                            vm.ShowRejectModal = false;
                        }
                        else {
                            vm.ShowAlert(vm.$t('Ticket Accepted Successfully'), "success", true, vm.$t('Alert'));
                        }
                    }
                    else if (response.data.code == "2") {
                        vm.ShowAlert(vm.$t('Ticket AcceptRejectDenied'), "warning", true, vm.$t('Warning'));
                        vm.showRejectTicketModal = false;
                    }
                    else {
                        if (type == "reject") {
                            vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                        }
                    }
                }
                else {
                    if (type == "reject") {
                        vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                    }
                    else {
                        vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                    }
                }
            }, function (response) {
            });
        },
