        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    break;
                case 'OpenFileModal':
                    this.OpenFileModal(item);
                    break;
                case 'ManageFollowUp':
                    this.ManageFollowUp(item.ticket_id, item);
                    break;
                case 'openAddNewEvent':
                    this.openAddNewEvent(item.ticket_id, item);
                    break;
                case 'openQuickViewpopup':
                    this.openQuickViewpopup(item.ticket_id, item.str_ticket_id, item.ticket_category_id, item.is_field_service);
                    break;
                case 'openViewRecurringTicket':
                    this.openViewRecurringTicket(item);
                    break;
                case 'AcceptRejectTicket':
                    this.AcceptRejectTicket(item, event.currentTarget.innerText);
                    break;
                case 'openViewTicketTab':
                    this.openViewTicketTab(item.ticket_id);
                    break;
                case 'MarkAsActive':
                    this.MarkAsActive(item.ticket_id);
            }
        },
        MarkAsActive: function (ID) {
            // debugger;
            if (ID != "" && ID != null) {
                var vm = this;
                vm.confirmR(vm.$t("AreYouSureToUpdateStatusActive"), true, 'Confirm', function () {
                    //    debugger;
                    var obj = {
                        CompanyId: vm.companyId,
                        UserId: vm.userid,
                        TicketIds: ID,
                        StatusId: 1111,
                    }
                    var objectPostString = JSON.stringify(obj);
                    DataServices.TicketingUpdateStatus(objectPostString).then(function (response) {
                        if (response.data != null && response.data != "") {
                            if (response.statusText == "OK") {
                                vm.fetchRecords();
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Status')]), "success", true, vm.$t('Alert'));
                                //$("#markActive").hide();
