                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "Ticketing",
                        subModuleCode: "Ticketing"
