<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Attachment') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>


                    <div class="modal-body">

                        <div class="heading-border">
                            <h5 class="h5"><span>Attachment</span><small class="float-right"><a href="javascript:;"
                                        id="showattachment" v-if="isDisabledButton == false" v-on:click="ShowAttachment" title="Show/Hide Add">
                                        <i class="fa fa-paperclip pr-2"></i>{{ $t('Show/Hide') }} {{ $t('Add')
                                        }}</a></small></h5>
                        </div>
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveAttachment"
                            v-if="ShowAttachmentData">

                        </dynamic-form>

                        <div class="listing pt-1" v-if="AttachmentList != null">
                            <TicketAttachment :AttachmentData="AttachmentList"></TicketAttachment>
                        </div>

                        <div class="text-danger text-center no-record float-left w-100" v-if="noRecord">
                            {{ $t('NoRecordfound') }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import TicketAttachment from "./TicketAttachment.vue";

export default {
    components: {
        TicketAttachment,
    },
    created: async function () {
        debugger;
        var vm = this;
        this.configData();
        vm.FetchAttachmentData();
    },
    props: {
        configdata: {
            type: Array,
            required: true
        },
        isDisabledButton:{
             type:Boolean,
             required:true
        },
        id: {
            type: Number,
            required: false
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            fileData: [],
            DisplayName: '',
            FileSrc: '',
            btnDisable: false,
            galleryImages: [],
            checkFileValidSize: false,
            checkFileValidExt: false,
            allowedPerFileSizeInMb: 0,
            disallowFileTypes: '',
            fileExt: '',
            fileSize: '',
            fileName: '',
            isLoading: false,
            TicketBindList: [],
            ShowAttachmentData: false,
            Description: null,
            AttachmentList: [],
            galleryFilesView: [],
            FileName: null,
            ListViewAttachment: 0,
            file: null,
            ticketModel: {
                NoteDescription: '',
                transferType: ''
            },
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Display Name'),
                            name: "DisplayName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "ListAttachment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: "",
                                multiple: false
                            },
                        }
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    methods: {
        Close: function () {
            var vueObj = this;
            vueObj.FormSchema[0].Data[0].value = null;
            vueObj.AttachmentfileName = '';
            vueObj.Attachmentfile = null;
            vueObj.AttachmentfileData = null;
            vueObj.FormSchema[0].Data[1].config.onChange = this.GetBase64String;
            this.$parent.showAttachmentModal = false;
            //this.$parent.FetchAttachmentData();
            this.$parent.noRecord = false;
        },
        SizeToMb: function (bytes) {
            // debugger;
            var vueObj = this;
            var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
            if (bytes > sizeInBuytes) {
                return -1;
            }
            else {
                var byesInMB = (bytes / 1024) / 1024;
                return byesInMB;
            }
        },
        configData: function (value) {
            debugger;
            var vm = this;
            var data = vm.configdata;
            $.each(data, $.proxy(function (index1, item) {
                if (item.CORE_CONFIG_KEY == "TicketFileSize") {
                    this.allowedPerFileSizeInMb = item.CONFIG_DATA_VALUE;
                }
                else if (item.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                    this.disallowFileTypes = item.CONFIG_DATA_VALUE;
                }
            }, this));

        },
        setFile: function (e) {
            debugger
            console.log(e.target.files);
            for (let index = 0; index < e.target.files.length; index++) {
                const element = e.target.files[index];
                this.file.push(element)
            }
        },
        ShowAttachment: function () {
            debugger;
            if (this.ShowAttachmentData == false) {
                this.ShowAttachmentData = true;
                this.noRecord = false;

            }
            else {
                this.ShowAttachmentData = false;
            }
        },
        FetchAttachmentData: function () {
            debugger;
            var vueObj = this;
            vueObj.galleryImages = [];
            vueObj.AttachmentList = [];
            vueObj.galleryFilesView = [];
            vueObj.isLoading = true;
            var url = `ticketId=${vueObj.id}`;
            DataServices.GetTicketFiles(url).then(function (response) {
                vueObj.isLoading = false;
                vueObj.AttachmentList = response.data.result.DATA;
                vueObj.$parent.fetchRecords();
                if (vueObj.AttachmentList != null && vueObj.AttachmentList.length > 0) {
                    // vueObj.galleryFilesView = $.grep(vueObj.AttachmentList, function (element, index) {
                    vueObj.noRecord = false;                    
                    //     return (JSON.parse(element.ATTACHMENT_DESC).Extension != 'pdf' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'word' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'excel' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'txt');                                
                    // });
                    // $.each(vueObj.galleryFilesView, $.proxy(function (index1, item) {
                    //     vueObj.galleryImages.push(item.ATTACHMENT_PATH);
                    // }, this));

                }
                else {
                    vueObj.AttachmentList = null;
                    vueObj.noRecord = true;
                }
                vueObj.isLoading = false;
            });
            vueObj.isLoading = false;
        },
        GetBase64String: function (evt) {
            debugger;
            try {
                // debugger;
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    debugger;
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        // debugger;
                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            id: vm.id,
                            moduleName: vm.modulename,
                            subModuleCode: vm.submodulecode,
                            base64String: base64StringEncoded,
                            fileSize: fileSize
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        SaveAttachment: function () {
            debugger;
            var vueObj = this;
            var extArray = vueObj.disallowFileTypes.split(',');
            if (extArray.includes(vueObj.fileExt)) {
                vueObj.checkFileValidExt = false;
                vueObj.ShowAlert(vueObj.$t("InvalidFileUploadMsg", vueObj.fileName, vueObj.disallowFileTypes), "warning", true, vueObj.$t("Alert"));
            }
            else {
                vueObj.checkFileValidExt = true;
            }

            var fileSizeInMB = this.SizeToMb(vueObj.fileSize);

            if (fileSizeInMB > parseInt(vueObj.allowedPerFileSizeInMb, 10)) {
                vueObj.checkFileValidSize = false;
                vueObj.ShowAlert(vueObj.$t("ExceedAllowedSize", vueObj.fileName, vueObj.allowedPerFileSizeInMb), "warning", true, vueObj.$t("Alert"));
            }
            else {
                vueObj.checkFileValidSize = true;
            }

            if (fileSizeInMB < 0) {
                vueObj.checkFileValidSize = false;
                vueObj.ShowAlert(vueObj.$t("ExceedAllowedSize", vueObj.fileName, vueObj.allowedPerFileSizeInMb), "warning", true, vueObj.$t("Alert"));

            } else {
                vueObj.checkFileValidSize = true;
            }

            if (vueObj.checkFileValidExt == true && vueObj.checkFileValidSize == true) {
                var objectPostString = JSON.stringify(vueObj.fileData);
                DataServices.UploadFile(objectPostString).then((response) => {
                    debugger;
                    if (response.data != null && response.data != "" && response.data.Status == "Success") {
                        vueObj.ShowAlert(vueObj.$t("Attachment successfully added"), "success", true, vueObj.$t("Alert"));
                        vueObj.DisplayName = '';
                        vueObj.FileSrc = '';
                        $("input[type=text]").val('');
                        //vueObj.$validator.reset();
                        vueObj.Close();
                        vueObj.callbackfunction();

                    }
                    else {
                        vueObj.FileSrc = '';
                        $("input[type=text]").val('');
                    }
                }, function (response) {
                });
            }
        },
    }
}
</script>
